const LONG_CB_MS = 500;
const PureComponent = class PureComponent extends React.PureComponent {
    timers = {};
    events = {};
    componentWillUnmount(){
        let t0 = Date.now();
        let a = Object.keys(this.timers);
        a.forEach(name=>this.clear_timer(name));
        a = Object.keys(this.events);
        a.forEach(event=>this.off(event));
        if (this._componentWillUnmount)
            this._componentWillUnmount();
        let t1 = Date.now();
        if (t1-t0 > LONG_CB_MS)
        {
            zerr.warn('long cb componentWillUnmount %s took %sms',
                this.displayName, t1-t0);
        }
        if (this.sp)
        {
            this.sp.return();
            delete this.sp;
        }
    }
    etask(task){
        this.sp = this.sp || etask('Component_'+this.displayName,
            function*_pure_component_etask(){ yield this.wait(); });
        this.sp.spawn(task);
        return task;
    }
             
    has_timer(name){ return !!this.timers[name]; }
    set_timer(name, cb, ms){
        this.clear_timer(name);
        this.timers[name] = {cb: cb, ms: ms,
            timer: setTimeout(()=>{ this.on_timer(name); }, ms)};
    }
    clear_timer(name){
        let t = this.timers[name];
        if (!t)
            return;
        clearTimeout(t.timer);
        delete this.timers[name];
    }
    on_timer(name){
        let t = this.timers[name];
        if (!t)
            return zerr('timer not found %s', name);
                                                           
        try {
            let t0 = Date.now();
            t.cb();
            let t1 = Date.now();
            if (t1-t0 > LONG_CB_MS)
            {
                zerr.warn('long cb timer %s:%s took %sms',
                    this.displayName, name, t1-t0);
            }
        } catch(e){ zerr('timer %s error %s', name, e); }
        this.clear_timer(name);
    }
            
    on(obj, event, cb){
        let _this = this;
        this.off(obj, event, cb);
        let e = this.events[event] = this.events[event]||{a: []};
        let cb2 = function(){
                                      
            let t0 = Date.now();
            let ret = cb.apply(this, arguments);
            let t1 = Date.now();
            if (t1-t0 > LONG_CB_MS)
            {
                zerr.warn('long cb event %s:%s took %sms', _this.displayName,
                    event, t1-t0);
            }
            return ret;
        };
        e.a.push({obj: obj, cb: cb, cb2: cb2});
        obj.on(event, cb2);
    }
    off(obj, event, cb){
        if (event===undefined && cb===undefined)
        {
            event = obj;
            let e = this.events[event];
            if (!e)
                return;
            let a = Object.assign([], e.a);
            return a.forEach(ee=>this.off(ee.obj, event, ee.cb));
        }
        if (event==undefined || cb===undefined)
            throw new Error('invalid off params');
        let e = this.events[event];
        if (!e)
            return;
        let i = e.a.findIndex(ee=>ee.cb===cb && ee.obj===obj);
        if (i==-1)
            return;
        let ee = e.a.splice(i, 1)[0];
        ee.obj.off(event, ee.cb2);
        if (!e.a.length)
            delete this.events[event];
    }
};

         
                   
                 
                               
E.T = props=>{
    if (typeof props=='string')
        return T(props);
    const {children} = props;
    if (typeof children=='string')
        return T(children.replace(/\s+/g, ' '));
    if (Array.isArray(children))
    {
        let obj = [], str = [];
        for (let v of children)
        {
            if (typeof v=='string')
                str.push(v);
            else
            {
                obj.push(v);
                str.push('$'+obj.length);
            }
        }
        let translated = T(str.join('')).split(/(\$\d+)/);
        return translated.map(v=>{
            let m = v.match(/\$(\d+)/);
            return m ? obj[m[1]-1] : v;
        });
    }
    console.error('<T> must receive text to translate. Received: ', children);
    return null;
};

