function Tooltip(props){
    let {position = 'top', icon_cls, icon_div_cls} = props;
    let classes = ['popup-tooltip', `popup-tooltip-${position}`];
    return <div className={classnames(classes)}>
      <div className='header'>
        <div className={icon_div_cls||'f32'}>
          <i className={icon_cls}></i>
        </div>
        <h2>{props.title}</h2>
      </div>
      <div className='body'>
        {props.children}
      </div>
    </div>;
}

