class Main_switch extends PureComponent {
    state = {};
    componentDidMount(){ this.setState({value: !!this.props.value}); }
    render_tooltip({is_protect}){
        let {root_url, value, is_mitm, is_protect_pc,
            is_protect_browser} = this.props;
        let country = zcountry.code2label(this.props.country || '');
        let src_country = zcountry.code2label(this.props.src_country || '');
        let flag_country = !is_mitm && !is_protect;
        let tooltip_opts = {
            position: 'bottom',
            icon_div_cls: flag_country ? 'fsvg_4x3' : 'f32',
            icon_cls: classnames('flag', this.props.country, {
                'flag-country': flag_country,
                'flag-unblock': is_mitm,
                'flag-protect': is_protect,
            }),
        };
        if (is_protect)
        {
            if (is_protect_pc)
            {
                return <Tooltip {...tooltip_opts}
                  title={<span>All your PC traffic is secured</span>}>
                  <p>Click to stop protecting your PC traffic</p>
                </Tooltip>;
            }
            if (is_protect_browser)
            {
                return <Tooltip {...tooltip_opts}
                  title={<span>All your browser traffic is secured</span>}>
                  <p>Click to stop protecting your browser traffic</p>
                </Tooltip>;
            }
            if (value)
            {
                return <Tooltip {...tooltip_opts}
                  title={<span>Your connection with <b>{root_url}</b> is
                    secured</span>}>
                  <p>Click to stop protecting your connection
                    with <b>{root_url}</b></p>
                </Tooltip>;
            }
            return <Tooltip {...tooltip_opts}
              title={<span>Secure your connection</span>}>
              <p>Click to encrypt and protect all your traffic
                with <b>{root_url}</b></p>
            </Tooltip>;
        }
        if (is_mitm)
        {
            if (value)
            {
                return <Tooltip {...tooltip_opts}
                  title={<span><b>{root_url}</b> is unblocked</span>}>
                  <p>Click to stop VPN and stop unblocking {' '}
                      <b>{root_url}</b></p>
                </Tooltip>;
            }
            return <Tooltip {...tooltip_opts}
              title={<span><b>{root_url}</b> is blocked by your
                country</span>}>
              <p>Click to start VPN for <b>{root_url}</b> and unblock
                it</p>
            </Tooltip>;
        }
        if (value)
        {
            return <Tooltip {...tooltip_opts}
              title={<span><b>{root_url}</b> country is {country}</span>}>
              <p>Click to stop VPN for <b>{root_url}</b> and change
                country back to {src_country}</p>
            </Tooltip>;
        }
        return <Tooltip {...tooltip_opts}
          title={<span>Change country to {country}</span>}>
          <p>Click to start VPN for <b>{root_url}</b> and change
            country to {country}</p>
        </Tooltip>;
    }
    render(){
        let {inside} = this.state;
        let {type = 'unblock', tooltip, value} = this.props;
        let s = classnames({[`${type}-switch`]: 1, active: value, inside});
        let is_protect = type=='protect';
        return <div className='main-switch'>
          <div className={s} onClick={this.onClick}
            onMouseEnter={this.onMouseEnter} onMouseLeave={this.onMouseLeave}>
            {is_protect ? T('Protect') : T('Unblock')}
          </div>
          {!!tooltip && this.render_tooltip({is_protect})}
        </div>;
    }
    onMouseLeave = e=>this.setState({inside: false});
    onClick = e=>{
        let value = !this.props.value;
        this.setState({inside: true});
        if (this.props.onClick)
        {
            this.props.onClick.call(this, {type: 'hola', event: e, value,
                ref: this});
        }
    };
}

class Trial_timer extends PureComponent {
    constructor(props){
        super(props);
        this.state = {trial_left: this.get_trial_left()};
        this.timer_tick();
    }
    componentWillUnmount(){
        if (this.timer)
            this.timer = clearTimeout(this.timer);
    }
    get_trial_left(){
        let trial = this.props.trial;
        return trial ? Math.max(trial.expire_ts-Date.now(), 0) : 0;
    }
    timer_tick = ()=>{
        let trial_left = this.get_trial_left();
        this.setState({trial_left});
        this.timer = trial_left>0 ? setTimeout(this.timer_tick, date.ms.SEC) :
            null;
    };
    onClick = ()=>{
        const be_bg_main = window.popup_main&&window.popup_main.be_bg_main;
        if (be_bg_main)
            be_bg_main.gclid_ga_event('go_plus', 'timer');
    };
    render(){
        let {state, props} = this;
        if (!state.trial_left)
            return null;
        let ref = 'trial_timer_'+props.root_url.replace(/\./g, '_');
        return [
          <a className="trial-timer" target='_blank' rel='noopener noreferrer'
            href={plus_ref(ref, {root_url: props.root_url})} key="timer"
            onClick={this.onClick}>
            {common_ui.format_time(state.trial_left)}
          </a>,
          <Tooltip position="bottom" title="Upgrade to PLUS!"
            key="tooltip">
            <p>Get unlimited unblocking time for {props.root_url}.</p>
          </Tooltip>
        ];
    }
}

