class Popular_view extends PureComponent {
    state = {list: [], top_urls: []};
    componentDidMount(){
        this.on(api, 'change:unblocking_rate', this.update_list);
        this.update_list();
        perr({id: 'be_ui_popular_view', info: {root_url: api.get_root(),
            url: api.get_url()}});
    }
    update_list = ()=>{
        let _this = this;
        etask(function*(){
            let list = api.get('unblocking_rate')||[];
            let root_urls = list.map(p=>p.root_url);
            let list_ps = yield api.be_premium.ecall('get_force_premium_rules',
                [root_urls, {ignore_install_version: true}]);
            list = list.map((p, i)=>{
                let site_conf = be_util.get_site_conf(api.be_ext, p.root_url);
                return {...p,
                    is_ps: zutil.get(site_conf, 'require_plus') || list_ps[i]};
            });
            _this.setState({list, top_urls: root_urls});
        });
    };
    on_focus = ()=>{
        perr({id: 'be_ui_popular_input_focus', info: {
            root_url: api.get_root(), url: api.get_url()}});
    };
    on_redirect = search=>etask(function*(){
        perr({id: 'be_ui_popular_click_go', info: {
            root_url: api.get_root(), url: api.get_url(), search}});
        yield api.be_tabs.ecall('set_force_suggestion',
            [api.get_tab_id(), true]);
        B.tabs.update(api.get_tab_id(), {url: search, active: true});
        api.ui_popup.close_popup();
    });
    render(){
        let {list, top_urls} = this.state;
        return <div className='popular-view'>
          <common_ui.Search_field on_focus={this.on_focus}
            on_redirect={this.on_redirect} top_urls={top_urls}/>
          <div className='popup-popular-list'>
            {list.map(p=><Popular_view_item key={p.root_url} {...p}/>)}
          </div>
          <div className='popular-view-footer'>
            <a href={api.get_popular_url()} target='_blank'
              rel='noopener noreferrer'>
              More sites in your country...
            </a>
          </div>
        </div>;
    }
}

