let is_offline = ()=>window.navigator.onLine === false;

class Alert_error extends React.PureComponent {
    state = {};
    update_status = ()=>
        this.setState({offline: is_offline()});
    componentDidMount(){
        this.update_status();
        window.addEventListener('offline', this.update_status);
        window.addEventListener('online', this.update_status);
        perr({id: 'be_ui_err', info: {offline: is_offline()}});
    }
    componentWillUnmount(){
        window.removeEventListener('offline', this.update_status);
        window.removeEventListener('online', this.update_status);
    }
    render(){
        let {props} = this;
        let {offline} = this.state;
        let on_yes = ()=>{
            let be_popup_lib = window.popup_main.be_popup_lib;
            perr({id: 'be_ui_popup_reload'});
            be_popup_lib.reload_ext({info: 'error'}, true);
        };
        if (offline)
        {
            return <Modal_view cls='alert-error'
              on_no={props.on_close} text_no={T('Close')}>
              <p><b>No internet</b></p>
              <p className='list-header'>Try:</p>
              <ul className='with-header'>
                <li>Checking the network cables, modem, and router</li>
                <li>Reconnecting to Wi-Fi</li>
              </ul>
            </Modal_view>;
        }
        return <Modal_view cls='alert-error' on_no={props.on_close}
          on_yes={on_yes} text_no={T('Close')}
          text_yes={T('Reload extension')}>
          <p>Sorry, Hola is not available right now.</p>
          <ol>
            <li>Check you have Internet connection</li>
            <li><a onClick={on_yes}>Reload</a> the extension</li>
            <li>Email us at <E.Link_mailto to='support@hola.org'/></li>
          </ol>
        </Modal_view>;
    }
}

