E.is_mouse_over = (el, event)=>{
    if (!el)
        return false;
    let r = el.getBoundingClientRect();
    let x = event.clientX, y = event.clientY;
    return x>=r.left && x<=r.right && y>=r.top && y<=r.bottom;
};

E.Close_hint = class Close_hint extends PureComponent {
    state = {show: false, hover: false};
    componentDidMount(){
        $('body').on('mousemove', this.on_body_mouse_move);
        this.props.be_tpopup.on('mouseleave', this.on_mouse_leave);
        if (this.props.show_on_init)
            this.setState({show: true});
    }
    componentWillUnmount(){
        $('body').off('mousemove', this.on_body_mouse_move);
        this.props.be_tpopup.off('mouseleave', this.on_mouse_leave);
        clearTimeout(this.timer);
    }
    componentDidUpdate(prev_props, prev_state){
        if (prev_state.hover && !this.state.hover)
            this.timer = setTimeout(()=>this.setState({show: false}), 300);
        if (!prev_state.hover && this.state.hover)
            this.timer = clearTimeout(this.timer);
    }
    on_body_mouse_move = e=>{
        let over_close_btn = E.is_mouse_over(this.props.close_btn, e);
        this.setState({hover: E.is_mouse_over(this.el, e) ||
            E.is_mouse_over(this.arrow_el, e) || over_close_btn});
        if (!this.state.show && over_close_btn)
            this.setState({show: true});
    };
    on_mouse_leave = e=>this.setState({hover: false});
    on_click_site = ()=>this.props.on_click(this.props.root_url);
    on_click_all = ()=>this.props.on_click('all');
    on_exited = ()=>{
        if (this.props.on_hide)
            this.props.on_hide();
    };
    render(){
        let {props, state} = this;
        let period = props.period;
        switch (date.str_to_dur(props.period))
        {
        case date.ms.DAY: period = 'one day'; break;
        case date.ms.WEEK: period = 'one week'; break;
        case date.ms.MONTH: period = 'one month'; break;
        case date.ms.YEAR: period = 'one year'; break;
        }
        let style = {display: 'block'}, arrow_style = {};
        if (props.offset)
        {
            style.right = -props.offset+'px';
            arrow_style.right = 10+props.offset+'px';
        }
        return <CSSTransition in={state.show} classNames="popup-hint"
          onExited={this.on_exited} mountOnEnter={true} unmountOnExit={true}
          timeout={150}>
          <div className="popup-hint popup-header-close-hint"
            style={style} ref={el=>this.el = el}>
            <div className="hint_close_arrow" style={arrow_style}
              ref={el=>this.arrow_el = el}/>
            <div className="hint_dont_show"><E.T>Don't show again</E.T></div>
            <div className="hint_option" onClick={this.on_click_site}>
              <E.T>for <b>{props.root_url}</b> for {period}</E.T>
            </div>
            <div className="hint_option" onClick={this.on_click_all}>
              <E.T>for <b>any site</b> for {period}</E.T>
            </div>
          </div>
        </CSSTransition>;
    }
};

