let close_hint_el;
E.render_close_hint = function(opt={}){
    let parent = $('#tpopup_close').parent();
    close_hint_el = close_hint_el ||
        $('<div class=hint-root/>').appendTo(parent);
    ReactDOM.render(ReactDOM.createPortal(<E.Close_hint {...opt}/>, parent[0]),
        close_hint_el[0]);
};

E.remove_close_hint = function(){
    if (!close_hint_el)
        return;
    ReactDOM.unmountComponentAtNode(close_hint_el[0]);
    close_hint_el.remove();
    close_hint_el = null;
};

E.alert_error = ()=>etask(function*(){
    let div = document.createElement('div');
    document.body.appendChild(div);
    this.finally(()=>{
        ReactDOM.unmountComponentAtNode(div);
        document.removeChild(div);
    });
    ReactDOM.render(<Alert_error on_close={()=>this.return()}/>, div);
    yield this.wait();
});

export default E;
